If you are coming please whatapp me, so that I could schedule my time for you, 
We hope to see you there soon!` },

            ],
            messageCn: [
                { code: "si", msg: `你好！我是Victova房地产的Alvin Tan。感谢您最近通过我们的Facebook广告询问Semenyih的“Springvale”排屋。我可以协助您解答任何疑问。如果您这个周末有空，我很乐意在我们的展厅与您会面，提供进一步的帮助并展示房产。我们可以在这个周六或周日的任何方便时间见面吗？` },
                {
                    code: "lc1", msg: `我们期待在以下地址欢迎您的到来：
Lot 46701, Jalan Broga, Semenyih, 43500 Kajang, Selangor
为了方便您找到我们，您可以使用以下链接在Google地图和Waze上找到我们：

