请在您前来时给我打电话或发WhatsApp信息。希望很快能见到您！` },
                {
                    code: "lc2", msg: `我期待明天在我们的展厅和项目地点迎接您：
Lot 46701, Jalan Broga, Semenyih, 43500 Kajang, Selangor
为方便您找到我们，您可以在 Google 地图和 Waze 上找到我们：

