希望很快能见到您!`},
                {
                    code: "lc3", msg: `您好！我是来自 Victova 房地产的 Alvin Tan。感谢您通过我们的 Facebook 广告咨询有关士毛月 "Springvale" 排屋的相关信息。
我在这里帮助您解答任何疑问。如果您这个周末有空，我很乐意在我们的展厅与您见面，为您提供进一步的帮助。
以下是我们的展厅地址和位置：

